/* eslint-disable @typescript-eslint/no-explicit-any */
export default class Database {
    // eslint-disable-next-line no-useless-constructor
    constructor(db) {
        this.db = db;
        this.orderManagerCollection = () => this.db().collection('samwise-order-manager');
        this.settingsCollection = () => this.db().collection('samwise-settings');
        this.bannerMessageStatusCollection = () => this.db().collection('samwise-banner-message');
        this.tagsCollection = () => this.db().collection('samwise-tags');
        this.tasksCollection = () => this.db().collection('samwise-tasks');
        this.subTasksCollection = () => this.db().collection('samwise-subtasks');
        this.groupsCollection = () => this.db().collection('samwise-groups');
        this.pendingInvitesCollection = () => this.db().collection('samwise-group-pending-invites');
    }
}
